import React, { useMemo, useCallback } from 'react';
import {
  IconVerticalAlignBottom,
  IconVerticalAlignMiddle,
  IconVerticalAlignTop,
} from '../icons';

import { IconSelect, IconSelectSingleLineDefaultWidth } from './Basics';

export type VerticalAlign = 'middle' | 'top' | 'bottom';

export interface IVerticalAlignSelectProps
  extends Omit<React.ComponentProps<typeof IconSelect>,
  'onChange' | 'options' | 'overlayVisible' | 'onVisibleChange' | 'width' | 'value'> {
  /**
   * 对齐方式修改时的回调函数
   */
  onChange: (align: VerticalAlign) => void;
  /**
   * 选项的tooltip提示
   */
  options: Record<VerticalAlign, { title: string, icon?: React.ReactElement }>;

  value: VerticalAlign;
}

interface ISelectOption {
  title: string;
  key: string;
  value: VerticalAlign;
  icon: React.ReactElement;
}

export const VerticalAlignSelect: React.FC<IVerticalAlignSelectProps> = (props) => {
  const {
    onChange,
    options,
    value,
    ...rest
  } = props;

  // 垂直对齐的选项
  const normalizeOptions: ISelectOption[] = useMemo(
    () => [
      {
        title: options.top.title,
        value: 'top',
        key: 'top',
        icon: options.top.icon || <IconVerticalAlignTop />,
        testid: 'option-top',
      },
      {
        title: options.middle.title,
        value: 'middle',
        key: 'middle',
        icon: options.middle.icon || <IconVerticalAlignMiddle />,
        testid: 'option-middle',
      },
      {
        title: options.bottom.title,
        value: 'bottom',
        key: 'bottom',
        icon: options.bottom.icon || <IconVerticalAlignBottom />,
        testid: 'option-bottom',
      },
    ],
    [options]
  );



  const handleChange = useCallback(
    (e) => {
      if (onChange) {
        onChange(e);
      }
    },
    [onChange]
  );

  return (
    <IconSelect
      value={value}
      onChange={handleChange}
      testid='VerticalAlignSelect'
      options={normalizeOptions}
      // width={IconSelectSingleLineDefaultWidth}
      {...rest}
    />
  );
};

